.header {
  display: flex;
  position: fixed;
  z-index: 55;
  top: 0;
  left: 0;
  right: 0;
  justify-content: space-between;
  align-items: center;
  padding: 25px;
  padding-top: 15px;
}

.header__logoImg {
  object-fit: contain;
  flex: 1;
  width: 90px;
  margin-right: 100px;
}

.header__links {
  display: flex;
  gap: 15px;
}

.header__links > a {
  text-decoration: none;
  text-transform: uppercase;
  color: black;
  font-weight: 500;
  font-size: 15px;
  padding: 8px 10px;
  border-radius: 10px;
  transition: all 0.5s ease-in-out;
}
.header_links {
  text-decoration: none;
  text-transform: uppercase;
  color: black;
  font-weight: 500;
  font-size: 15px;
  padding: 8px 10px;
  border-radius: 10px;
}
.header__modelNames:hover {
  background: rgb(214, 214, 214);
}

.header__right {
  display: flex;
  align-items: center;
  gap: 15px;
}

.header__right > a {
  text-decoration: none;
  text-transform: uppercase;
  color: black;
  font-weight: 500;
  font-size: 15px;
  z-index: 0;
  position: relative;
}

.header__link--hidden {
  visibility: hidden;
}

.header__menu > .MuiSvgIcon-root {
  position: relative;
  z-index: 2;
  cursor: pointer;
}
